.drawer-container {
    top: 68px;
    width: 250px;
    box-sizing: border-box;
    overflow-y: auto;
    height: 100vh;
    overflow-y: auto;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    height: 100vh;
    transition: background 0.3s ease, backdrop-filter 0.3s ease;
    background-color: var(--color-bg);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    padding-bottom: 30px;
}

.MuiListItemButton-root {
    display: flex;
    justify-content: space-between;
    transition: background-color 0.3s ease, color 0.3s ease;
}
.MuiListItemText-root {
    flex-grow: 1;
}
.MuiSvgIcon-root {
    cursor: pointer;
}

.MuiCollapse-wrapperInner {
    padding-left: 10px;
}


.history-title {
    margin-top: 16px;
    padding-left: 16px;

}
.history-list {
    max-height: 250px;
    /* Set a fixed height for the scrollable area */
    overflow-y: auto;
    /* Enable vertical scrolling */
    scrollbar-width: thin;
    /* Thin scrollbar for Firefox */
    scrollbar-color: #007bff transparent;
    /* Blue scrollbar for Firefox */
}

/* Custom scrollbar styles for WebKit-based browsers (Chrome, Safari) */
.history-list::-webkit-scrollbar {
    width: 5px;
    /* Thin scrollbar width */
}

.history-list::-webkit-scrollbar-thumb {
    background-color: #007bff;
    /* Blue color for the scrollbar */
    border-radius: 4px;
    /* Rounded corners for smooth look */
    min-height: 5px;
    /* The minimum height of the scroll thumb */
}

.history-list::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0.1);
    /* Track with subtle color for contrast */
    border-radius: 4px;
    margin-top: 5px;
    /* Small margin at the top of the track */
    margin-bottom: 5px;
    /* Small margin at the bottom of the track */
}
.active-item {
    background-color: #343a40;
    color: black;
    border-left: 4px solid #007bff;
    font-weight: bold;
    transition: background-color 0.3s ease, color 0.3s ease, border-left 0.3s ease;
}
.active-item:hover {
    background-color: #495057;
    color: #000000;
    font-weight: bold;
}
.active-item .MuiListItemText-root {
    color: #ffffff;
    font-weight: bold;
}

.active-item .MuiSvgIcon-root {
    fill: #007bff;
} 

/* Hide action icons by default */
.assistant-item .MuiIconButton-root {
    visibility: hidden;
    /* Hide icons initially */
    transition: visibility 0.2s ease, opacity 0.2s ease;
    opacity: 0;
}

/* Show the icons when the item is hovered */
.assistant-item:hover .MuiIconButton-root {
    visibility: visible;
    opacity: 1;
}

/* When history is open, keep the icons visible */
.assistant-item.open .MuiIconButton-root {
    visibility: visible;
    opacity: 1;
}
/* Hide the three dots by default */
.history-list .MuiIconButton-root {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.2s ease, opacity 0.2s ease;
}

/* Show the three dots only when the specific history item is hovered */
.history-list .MuiListItemButton-root:hover .MuiIconButton-root {
    visibility: visible;
    opacity: 1;
}


/* Keep the three dots visible when the IconButton or the Menu is focused */
.history-list .MuiIconButton-root,
.history-list .MuiIconButton-root:focus,
.history-list .MuiIconButton-root:active {
    visibility: visible;
    opacity: 1;
}

/* Hide the three dots by default */
.history-list .MuiIconButton-root {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.2s ease, opacity 0.2s ease;
}

/* Show the three dots when the list item is hovered */
.history-list .MuiListItemButton-root:hover .MuiIconButton-root {
    visibility: visible;
    opacity: 1;
}

/* Hide action icons by default */
.assistant-item .MuiIconButton-root {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.2s ease, opacity 0.2s ease;
}

/* Show the icons when the item is hovered (for desktop) */
.assistant-item:hover .MuiIconButton-root {
    visibility: visible;
    opacity: 1;
}

/* Show the icons when the list item is long-pressed (for mobile) */
.assistant-item.long-press .MuiIconButton-root {
    visibility: visible;
    opacity: 1;
}

/* Keep the icons visible when the item is open */
.assistant-item.open .MuiIconButton-root {
    visibility: visible;
    opacity: 1;
}